export default class CANBusGenerator {
  private intervalID;

  start(onMessage): void {
    if (!this.intervalID) {
      this.intervalID = setInterval(() => {
        setTimeout(() => {
          const message = "hello world";
          onMessage(message);
        }, Math.random() * 100 - 50);
      }, 500);
    }
  }

  stop(): void {
    if (this.intervalID) {
      clearInterval(this.intervalID);
      this.intervalID = null;
    }
  }
}
